.centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-elements-container {
    border-radius: 16px;
    -webkit-box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
}

.card-elements-container .cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    margin: 20px 14px;
}

.card-elements-container .left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
}

.card-elements-container .text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.29;
    letter-spacing: -0.4px;
    margin-left: 8px;
    color: #000;
}

.card-elements-container .right {
    align-self: center;
}